/* Estilo para el contenedor del formulario */
.TodoForm-container {
    background-color: #e6e6fa; 
    padding: 20px; 
    border-radius: 8px; 
    box-shadow: 0 4px 8px rgba(243, 237, 237, 0.2); /* Sombra */
    max-width: 400px; 
    margin: 0 auto; 
    font-family: fantasy;
  }
  

  form {
    display: flex;
    flex-direction: column;
  }
  
  label {
    font-size: 18px;
    margin-bottom: 8px;
    color: black;
  }
  
  textarea {
    padding: 8px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 4px;
    resize: vertical;
    min-height: 100px;
    margin-bottom: 16px;
    font-family: fantasy;
  }
  
  .TodoForm-buttonContainer {
    display: flex;
    justify-content: space-between;
    margin-top: 16px;
  }
  
  .TodoForm-button {
    padding: 8px 16px;
    font-size: 16px;
    cursor: pointer;
    border: none;
    border-radius: 4px;
    transition: background-color 0.3s ease;
  }
  
  .TodoForm-button--cancel {
    background-color: #d96c92;
    color: #fff;
    margin-right: 10px;
    margin-left: 15px;
    font-family: fantasy;
  }
  
  .TodoForm-button--cancel:hover {
    background-color: #ae5082;
  }
  
  .TodoForm-button--add {
    background-color: #6a6cd9;
    color: #fff;
    font-family: fantasy;
  }
  
  .TodoForm-button--add:hover {
    background-color: #4552a3;
  }
  