.loading-dots {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
  }
  
  .dot {
    width: 20px;
    height: 15px;
    background-color:#a979e3;
    border-radius: 50%;
    margin: 0 5px;
    animation: bounce 0.5s infinite ease-in-out;
  }
  
  .dot1 {
    animation-delay: 0.1s;
  }
  
  .dot2 {
    animation-delay: 0.2s;
  }
  
  .dot3 {
    animation-delay: 0.3s;
  }
  
  @keyframes bounce {
    0%, 100% {
      transform: translateY(0);
    }
    50% {
      transform: translateY(-10px);
    }
  }
  